@import url("https://fonts.googleapis.com/css?family=Roboto+Mono:300,400,500,700");

:root {
	--header-bg: #93e993;
	--header-color: #000000;
	--main-bg: #ece3e3;
	--operators-bg: #93e993;
	--operators-bg-hover: #1dbb1d;
	--all-btn-bg: #ece3e3;
	--all-btn-hover: #d3d3d3;
	--body-bg: #d2d8d8;
	--box-shadow: 10px 10px 48px #97a1a1, -10px -10px 48px #ffffff;
	--label-text: #111;
}

[data-theme="dark"] {
	--header-bg: #111;
	--header-color: white;
	--main-bg: rgb(192, 187, 187);
	--operators-bg: rgb(114, 112, 112);
	--operators-bg-hover: rgb(65 59 59 / 24%);
	--all-btn-bg: rgb(192, 187, 187);
	--all-btn-hover: #d3d3d3;
	--body-bg: #333;
	--box-shadow: 10px 10px 48px #232323, -10px -10px 48px #23222085;
	--label-text: #fff;
}

body {

	background: var(--body-bg);
}

#calculator {
	font-size: 18px;
	font-family: "Roboto Mono";
	margin: auto;
	width: 330px;
	border-radius: 10px;
	box-shadow: var(--box-shadow);
    margin-top: 2vh;
	margin-bottom: 5rem;
}

header {
	padding: 20px;
	background: var(--header-bg);
	color: var(--header-color);
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	text-align: right;
	height: 80px;
}

header #detail {
	font-size: 1em;
}

header #current {
	font-size: 2em;
    overflow-x: auto;
    scrollbar-width: thin;
}

#keys {
	height: 100%;
	background: var(--main-bg);
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	overflow: auto;
}

#keys .operators {
	background-color: var(--operators-bg);
	margin-top: 2px;
}

#keys .operators:hover {
	background-color: var(--operators-bg-hover);
}

#keys .numbers {
	margin-top: 2px;
}

#keys #all_clear {
	width: 50%;
	margin-top: 2px;
}

#keys #clear {
	width: 50%;
	margin-top: 2px;
}

#keys button {
	font-size: 1.2em;
	font-family: "Roboto Mono";
	float: left;
	width: 25%;
	border: 0;
	padding: 1em;
	cursor: pointer;
	background: var(--all-btn-bg);
}

#keys button:hover {
	background: var(--all-btn-hover);
	transition: 0.4s all;
}

#keys button:focus {
	outline: none;
}

#keys button:active {
	box-shadow: 0 0 10px #cdcdcd;
}

/* switch button  */

input[type="button"] {
	border: 0;
	margin: 35px 40%;
	line-height: 2.5;
	padding: 0 20px;
	font-size: 1rem;
	text-align: center;
	color: var(--header-color);
	border-radius: 10px;
	background-color: var(--header-bg);
	box-shadow: var(--box-shadow);
	cursor: pointer;
}
h1 {
	font-size: 14;
}