.navbar {
    position: sticky;
    font-family: Aileron;
    z-index: 9999;
    background-color: white;
    /*background-image: url('./img/');*/
    top : 0;
    left: 0;
    overflow: hidden;
    padding-top: 4.5vh;

    /*line-height: 3vh;*/
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 9vh;
    box-shadow: 0 2px 5px rgba(30,30,30,0.6);
  }
  
  .navbar ul {
    display: flex;
    justify-content: space-between;
  }
  
  .navbar li {
    margin-left: 4%;
    display: inline-block;
    align-self: center;
    font-size: 1.2rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  
  .navbar a {
    text-decoration: none;
  }
  
  .navbar a:hover {
    color: rgb(36, 199, 21);
  }
  
  .navbar .link_1 {
    margin-right: 25%;
    font-size: 2rem;
    font-weight: 700;
    color: rgb(230, 162, 38);
    
  }

  .navbar :not(.link_1) {
    font-size: 1.25rem;
    color: rgb(61, 20, 29);
    padding-left: 2%;
  }
.game-board {
    display: flex;
    justify-content: center;
  }
  
  .game {
    display: flex;
    flex-direction: column;
    padding-top: 15vh;
  }

  
.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 48px;
    font-weight: bold;
    line-height: 34px;
    height: 68px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 68px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
ol, ul {
    padding-left: 30px;
  }
  
  .board-row {
    display: block;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
:root {
	--header-bg: #93e993;
	--header-color: #000000;
	--main-bg: #ece3e3;
	--operators-bg: #93e993;
	--operators-bg-hover: #1dbb1d;
	--all-btn-bg: #ece3e3;
	--all-btn-hover: #d3d3d3;
	--body-bg: #d2d8d8;
	--box-shadow: 10px 10px 48px #97a1a1, -10px -10px 48px #ffffff;
	--label-text: #111;
}

[data-theme="dark"] {
	--header-bg: #111;
	--header-color: white;
	--main-bg: rgb(192, 187, 187);
	--operators-bg: rgb(114, 112, 112);
	--operators-bg-hover: rgb(65 59 59 / 24%);
	--all-btn-bg: rgb(192, 187, 187);
	--all-btn-hover: #d3d3d3;
	--body-bg: #333;
	--box-shadow: 10px 10px 48px #232323, -10px -10px 48px #23222085;
	--label-text: #fff;
}

body {

	background: #d2d8d8;

	background: var(--body-bg);
}

#calculator {
	font-size: 18px;
	font-family: "Roboto Mono";
	margin: auto;
	width: 330px;
	border-radius: 10px;
	box-shadow: 10px 10px 48px #97a1a1, -10px -10px 48px #ffffff;
	box-shadow: var(--box-shadow);
    margin-top: 2vh;
	margin-bottom: 5rem;
}

header {
	padding: 20px;
	background: #93e993;
	background: var(--header-bg);
	color: #000000;
	color: var(--header-color);
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	text-align: right;
	height: 80px;
}

header #detail {
	font-size: 1em;
}

header #current {
	font-size: 2em;
    overflow-x: auto;
    scrollbar-width: thin;
}

#keys {
	height: 100%;
	background: #ece3e3;
	background: var(--main-bg);
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	overflow: auto;
}

#keys .operators {
	background-color: #93e993;
	background-color: var(--operators-bg);
	margin-top: 2px;
}

#keys .operators:hover {
	background-color: #1dbb1d;
	background-color: var(--operators-bg-hover);
}

#keys .numbers {
	margin-top: 2px;
}

#keys #all_clear {
	width: 50%;
	margin-top: 2px;
}

#keys #clear {
	width: 50%;
	margin-top: 2px;
}

#keys button {
	font-size: 1.2em;
	font-family: "Roboto Mono";
	float: left;
	width: 25%;
	border: 0;
	padding: 1em;
	cursor: pointer;
	background: #ece3e3;
	background: var(--all-btn-bg);
}

#keys button:hover {
	background: #d3d3d3;
	background: var(--all-btn-hover);
	transition: 0.4s all;
}

#keys button:focus {
	outline: none;
}

#keys button:active {
	box-shadow: 0 0 10px #cdcdcd;
}

/* switch button  */

input[type="button"] {
	border: 0;
	margin: 35px 40%;
	line-height: 2.5;
	padding: 0 20px;
	font-size: 1rem;
	text-align: center;
	color: #000000;
	color: var(--header-color);
	border-radius: 10px;
	background-color: #93e993;
	background-color: var(--header-bg);
	box-shadow: 10px 10px 48px #97a1a1, -10px -10px 48px #ffffff;
	box-shadow: var(--box-shadow);
	cursor: pointer;
}
h1 {
	font-size: 14;
}

:root {
    --bluebell: #979fd0;
}


.feed-page {
    display: flex;
    justify-content: space-around;
}


.feed-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
}

.feed-status {
    display: flex;
    justify-content: space-around;
    align-self: center;
    margin-top: 5vh;
    width: 85%;
    min-height: 75px;
    background-color: rgb(253, 253, 253);
    border-radius: 5px;
    padding: 2vh 1%;
    font-family: Aileron;
}

.feed-footer {
    min-height: 200px;
    background-color: rgb(32, 32, 32);
    margin-top: 5vh;
}

.dashboard {
    display: flex;
    background-color: aliceblue;
    border: 1px solid black;
}

.user-image-container {
    display: flex;
    -webkit-clip-path: circle(50%);
            clip-path: circle(50%);
}

.user-image {
    display: none;
    height: 75px;
    width: 75px;
    object-fit: cover;
    
}

.user-name {
    font-size: 1.5rem;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#loading {
    display: none;
}

.donut {
    width: 2rem;
    height: 2rem;
    margin: 2rem;
    border-radius: 50%;
    border: 0.3rem solid rgba(#979fd0, 0.3);
    border: 0.3rem solid rgba(var(--bluebell), 0.3);
    border-top-color: #979fd0;
    border-top-color: var(--bluebell);
    -webkit-animation: 1.5s spin infinite linear;
            animation: 1.5s spin infinite linear;
}


@font-face {
    font-family: Aileron;
    src: url("https://fontlibrary.org//face/aileron");
}

@-webkit-keyframes spin {
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

@keyframes spin {
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  

.post-container {
    display: flex;
    justify-content: center;
    
    
    margin-top: 2vh;
    
    
    
}


.post {
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: white;

    box-shadow: 10px 0 30px #23232307, -10px 0 30px #23222085;
    width: 540px;
    border-radius: 10px;
    padding: 10px 5px 20px 5px;
}

.post > .image {
    display: none;
    width: calc(33000%/350);
    resize: both;
    object-fit: cover;
}

.post-desc {
    text-align: center;
    margin-top: 2vh;
    
}

.blur {
    -webkit-animation-name: blur;
            animation-name: blur;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}

.fade {
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}

.fade-out {
    -webkit-animation-name: fadeOut;
            animation-name: fadeOut;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}

@-webkit-keyframes fadeOut{
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes fadeOut{
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@-webkit-keyframes fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

@keyframes fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @-webkit-keyframes blur {
    0% {
        -webkit-filter: blur(5px);
                filter: blur(5px);
        opacity: 0.7;
    }

    50% {
        -webkit-filter: blur(4px);
                filter: blur(4px);
    }

    60% {
        -webkit-filter: blur(3px);
                filter: blur(3px);
    }

    75% {
        -webkit-filter: blur(2px);
                filter: blur(2px);
    }

    90% {
        -webkit-filter: blur(1px);
                filter: blur(1px);
    }
    100% {
        opacity: 1;
        -webkit-filter: none;
                filter: none;
    }
}

  @keyframes blur {
    0% {
        -webkit-filter: blur(5px);
                filter: blur(5px);
        opacity: 0.7;
    }

    50% {
        -webkit-filter: blur(4px);
                filter: blur(4px);
    }

    60% {
        -webkit-filter: blur(3px);
                filter: blur(3px);
    }

    75% {
        -webkit-filter: blur(2px);
                filter: blur(2px);
    }

    90% {
        -webkit-filter: blur(1px);
                filter: blur(1px);
    }
    100% {
        opacity: 1;
        -webkit-filter: none;
                filter: none;
    }
}


